<template>
  <div class="sub-content account">
    <h2></h2>
    <div class="inner-box">
      <Form :model="formAccount" :label-width="90">
        <FormItem label="">{{userInfo.nickname}}</FormItem>
        <FormItem label="">{{userInfo.mobile}}</FormItem>
        <FormItem label="">
          <i-switch v-model="formAccount.sms" size="large">
            <span slot="open"></span>
            <span slot="close"></span>
          </i-switch>
          <p style="color: #ed3f14"></p>
        </FormItem>
        <FormItem>
          <Button type="primary"> </Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAccount',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formAccount: {}
    }
  },
  created () {
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



