<template>
  <div class="sub-content bind-domain">
    <h2></h2>
    <div class="inner-box">
      <p> CNZME  <span style="color: #ed3f14">proxy.dafuv.com</span></p>
      <p>cnamecname</p>
      <ul>
        <li><a href="#" target="_blank"> cname </a></li>
        <li><a href="#" target="_blank"> cname </a></li>
        <li><a href="#" target="_blank"> cname </a></li>
      </ul>
      <p>10</p>
      <Form
        ref="formBindDomain"
        :model="formBindDomain"
        :rules="ruleBindDomain"
        :label-width="120">
        <FormItem label="" prop="domain">
          <Input :readonly="userInfo.private_domain !== ''" v-model="formBindDomain.domain" placeholder="test.xxx.comhttp"/>
        </FormItem>
        <FormItem>
          <template v-if="userInfo.private_domain">
            <Button
              type="ghost"
              @click="handleUnbind"> </Button>
          </template>
          <template v-else>
            <Button
              type="primary"
              @click="handleBind('formBindDomain')"> </Button>
          </template>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageBindDomain',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formBindDomain: {
        domain: this.userInfo.private_domain
      },
      ruleBindDomain: {
        domain: [
          {required: true, message: '', trigger: 'click'},
          {message: '', pattern: /^.+\..+\..+$/, trigger: 'click'}
        ]
      }
    }
  },
  created () {
    console.log(this.userInfo.private_domain)
  },
  methods: {
    handleBind (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/api/userInfo/bindPrivateDomain', this.formBindDomain).then((res) => {
            if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
              this.$Message.success({
                content: '',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    },
    handleUnbind () {
      this.$ajax.get('/api/userInfo/unBindPrivateDomain').then((res) => {
        if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
          this.$Message.success({
            content: '',
            duration: 1,
            onClose () {
              window.location.reload()
            }
          })
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.bind-domain .inner-box {
  p {
    margin-bottom: 10px;
  }
  ul {
    list-style: disc;
    margin-bottom: 20px;
    padding-left: 32px;
  }
}
</style>



