<template>
  <div class="content app-setting">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release"></BreadcrumbItem>
        <BreadcrumbItem to="/apps"></BreadcrumbItem>
        <BreadcrumbItem></BreadcrumbItem>
      </Breadcrumb>
      <Card :padding="45" shadow>
        <div class="inner-box">
          <div class="app-icon">
            <div class="icon">
              <Upload
                ref="upload"
                :action="`//dafu.im/api/apps/icon`"
                name="icon"
                :data="{id}"
                :with-credentials="true"
                accept=".png"
                :format="['png']"
                :before-upload="handleBeforeUpload"
                :on-success="handleUploadSuccess"
                :on-error="handleUploadError">
                <div class="upload-btn">
                  <Icon type="ios-cloud-upload" size="52"></Icon>
                  <p></p>
                </div>
              </Upload>
              <img
                ref="icon"
                :src="`//pic.dafu.im/icons/${id}.png?${new Date(appData.update_dt).getTime()}&imageView2/1/w/120/h/120//ignore-error/1`"
                height="120"
                width="120"
                alt="">
            </div>
            <div class="app-info">
              <p><span>{{appData.ext === 'ipa' ? 'iOS' : 'Android'}}</span><span>{{appData.package_name}}</span></p>
              <p><span>{{appData.version}}</span><span>{{(appData.app_size / 1000000).toFixed(2)}}M</span><span>{{appData.download_num}}</span></p>
              <Button type="primary" shape="circle" size="small" style="width:7em" @click="handleUploadBtnClick"></Button>
            </div>
            <div class="app-btn">
              <p><Button type="primary" icon="upload" size="large" style="width:120px" @click="handleUpgrade"></Button></p>
              <p><Button type="ghost" icon="eye" size="large" style="width:120px" @click="handlePreviewTemplate"></Button></p>
            </div>
            <div class="app-alert clear">
              <Alert type="warning" show-icon style="float:left">iOS///<a
                href="http://wpa.qq.com/msgrd?v=3&amp;uin=3261682395&amp;site=qq&amp;menu=yes" target="_blank">
                QQ3261682395</a></Alert>
              <Alert v-if="violation" type="error" show-icon style="float:right">
                <Icon type="android-warning" slot="icon"></Icon>
                <template slot="desc"><span style="font-size:16px">APP</span></template>
              </Alert>
            </div>
          </div>
          <Tabs type="card" value="base">
            <TabPane label="" name="base">
              <Form
                ref="formSetting"
                :model="formSetting"
                :rules="ruleSetting"
                :label-width="120">
                <FormItem prop="app_name" label="APP">
                  <Input
                    type="text"
                    v-model="formSetting.app_name"
                    placeholder="APP"
                    style="width:400px;margin-right:10px" />
                  <span>APP</span>
                </FormItem>
                <FormItem prop="url" label="">
                  <Input
                    type="text"
                    v-model="formSetting.url"
                    placeholder="6"
                    style="width:400px;margin-right:10px">
                  <span slot="prepend">https://dafu.im/</span></Input>
                  <span style="position:absolute;left:410px;top:0">6</span>
                </FormItem>
                <FormItem label="">
                  <Input
                    type="text"
                    v-model="formSetting.contact"
                    placeholder="/QQ//"
                    style="width:400px;margin-right:10px" />
                  <span></span>
                </FormItem>
                <FormItem label="">
                  <Input
                    type="text"
                    v-model="formSetting.password"
                    style="width:400px;margin-right:10px" />
                  <span></span>
                </FormItem>
                <FormItem label="">
                  <Input
                    type="text"
                    v-model="formSetting.limit_num"
                    placeholder=""
                    style="width:400px;margin-right:10px" />
                  <span>[0]APP</span>
                </FormItem>
                <template v-if="appData.ext === 'ipa'">
                  <FormItem label="">
                    <Input
                      type="text"
                      v-model="formSetting.appstore_url"
                      placeholder=""
                      style="width:330px;margin-right:10px"
                      :disabled="appStoreUrlIsDisabled" /><Button type="primary" style="margin-right:10px" @click="appStoreUrlIsDisabled = false"> </Button><span>
                App Store</span>
                  </FormItem>
                </template>
                <FormItem label="APP">
                  <Input
                    type="textarea"
                    v-model="formSetting.app_intro"
                    placeholder=""
                    style="width:400px" />
                </FormItem>
                <FormItem label="">
                  <Input
                    type="text"
                    v-model="formSetting.remark"
                    placeholder=""
                    style="width:400px;margin-right:10px" />
                  <span>APP</span>
                </FormItem>
                <FormItem>
                  <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmit('formSetting')"> </Button>
                </FormItem>
              </Form>
            </TabPane>
            <TabPane label="" name="template">
              <ul class="template-list clear">
                <li
                  v-for="item in templates"
                  :key="item.id"
                  :class="[item.id === selected ? 'selected': '']"
                  @click="selected = item.id">
                  <img :src="item.img" alt="">
                  <span class="checkbox"><Icon type="checkmark" :size="28" color="#fff"/></span>
                </li>
              </ul>
              <Alert>QQ: <a href="http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes" target="_blank">32-6168-2395</a></Alert>
              <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmitTemplate"> 
              </Button><Button type="ghost" shape="circle" style="width:120px;margin-right:10px" @click="handlePreviewTemplate"> </Button>
            </TabPane>
          </Tabs>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import tpl0 from '../assets/template/images/default.png'
import tpl1 from '../assets/template/images/qipai2.jpg'
import tpl2 from '../assets/template/images/majiang.jpg'
import tpl3 from '../assets/template/images/buyu.jpg'

export default {
  name: 'PageAppsSetting',
  data () {
    return {
      id: '',
      violation: false,
      appData: {},
      appStoreUrlIsDisabled: true,
      uploadList: [],
      formSetting: {},
      ruleSetting: {
        app_name: [
          {required: true, message: 'App', trigger: 'click'}
        ],
        url: [
          {required: true, message: '', trigger: 'click'},
          {type: 'string', min: 6, message: '6', trigger: 'click'}
        ]
      },
      selected: '',
      //  
      templates: [
        {id: 'default', img: tpl0},
        {id: 'qipai2', img: tpl1},
        {id: 'majiang', img: tpl2},
        {id: 'buyu', img: tpl3}
      ]
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      this.id = this.$route.params.id
      this.$ajax.get('/api/apps/detail', {params: {id: this.id}}).then((res) => {
        if (res.data && res.data.code === '200') {
          this.appData = res.data.data
          this.selected = res.data.data.template || 'default'
          const {app_name, app_intro, password, url, appstore_url, contact, limit_num, remark} = res.data.data
          this.formSetting = {app_name, app_intro, password, url, appstore_url, contact, limit_num, remark}
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  mounted () {
    this.uploadList = this.$refs.upload.fileList
  },
  methods: {
    handleUpgrade () {
      this.$router.push(`/release?id=${this.id}`)
    },
    //  
    handleBeforeUpload () {
      const check = this.uploadList.length < 1
      if (!check) {
        this.$Message.warning(' 1 ')
      }
      return check
    },
    handleUploadSuccess () {
      this.$refs.icon.setAttribute('src', `//pic.dafu.im/icons/${this.id}.png?${new Date().getTime()}&imageView2/1/w/120/h/120//ignore-error/1`)
      this.$refs.upload.fileList = []
      this.uploadList = []
    },
    handleUploadError (err) {
      this.$Message.error(err)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.formSetting.id = this.id
          this.$ajax.post('/api/apps/update', this.formSetting).then((res) => {
            if (res.data && res.data.code === '200') {
              this.$Message.success({
                content: '',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    },
    handleSubmitTemplate () {
      this.$ajax.post('/api/apps/template', {
        id: this.id,
        template: this.selected
      }).then((res) => {
        if (res.data && res.data.code === '200') {
          this.$Message.success('')
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    handlePreviewTemplate () {
      // window.open(`/static/template.html?code=${this.selected}`)
    },
    handleUploadBtnClick () {
      this.$refs.upload.handleClick()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.app-setting {
  .app-icon {
    margin-bottom: 20px;
    border-bottom: 1px solid #f5f8fb;
  }
  .icon {
    position: relative;
    float: left;
    height: 120px;
    width: 120px;
    margin: 0 20px 20px 0;
    img {
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
    }
  }
  .app-info {
    float: left;
    margin-top: 5px;
    > p {
      margin-bottom: 20px;
      > span {
        display: inline-block;
        width: 150px;
        margin-right: 10px;
      }
    }
  }
  .app-btn {
    float: right;
    p {
      margin-bottom: 10px;
    }
  }
  .app-alert {
    clear: both;
    margin-bottom: 10px;
  }
  .ivu-upload {
    float: left;
    margin-right: 10px;
  }
}
.template-list {
  margin-bottom: 20px;
  li {
    position: relative;
    float: left;
    padding: 5px;
    max-width: 25%;
    border: 5px solid #fff;
    img {
      display: block;
      width: 100%;
      max-width: 264px;
      margin: auto;
    }
  }
  .selected {
    border-color: #2d8cf0;
  }
  .selected .checkbox {
    position: absolute;
    top: 15px;
    right: 15px;
    height: 28px;
    width: 28px;
    text-align: center;
    background: #2d8cf0;
    border: 1px solid #1c2438;
  }
}
.ivu-upload {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 120px;
  width: 120px;
  cursor: pointer;
}
.upload-btn {
  display: none;
  height: 120px;
  width: 120px;
  padding-top: 20px;
  text-align: center;
  color: #fff;
  background: rgba(0, 0, 0, .6);
  border-radius: 16%;
}
.ivu-upload:hover .upload-btn {
  display: block;
}

</style>



