<template>
  <div class="sub-content invoice-detail">
    <h2></h2>
    <div class="inner-box">
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.id}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{parseInt(invoiceInfo.price, 10).toLocaleString()}} </dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.company}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.no}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.address}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.name}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.phone}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.status}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.express}}</dd>
      </dl>
      <dl class="clear">
        <dt></dt>
        <dd>{{invoiceInfo.eid}}</dd>
      </dl>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageInvoiceDetail',
  props: {
    userInfo: Object
  },
  data () {
    return {
      invoiceInfo: {
        id: '12345',
        price: '2000',
        company: '',
        no: '0123456789',
        name: '',
        phone: '150*****010',
        address: '',
        status: '',
        express: '',
        eid: '9876543210'
      }
    }
  },
  created () {
    const invoiceId = this.$route.params.id
    console.log(invoiceId)
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.invoice-detail {
  dt, dd {
    float: left;
    padding: 5px 16px;
  }
  dt {
    width: 10em;
    text-align: right;
  }
}
.invoice-box {
  margin: 20px 0;
}
</style>



