<template>
  <div class="kit">
    <div class="banner">
      <img src="../assets/images/banner_kit.jpg" alt="">
      <div class="inner-box">
        <Row>
          <Col span="12">
          <h3>UDID</h3>
          <p>iOS<br/>
            safaridafu.im/udid</p>
          <Button type="primary" size="large" style="width: 160px" @click="$router.push('/udid')"></Button>
          </Col>
          <Col span="12">
          <h3>iPA</h3>
          <p>iPAiPA</p>
          <Button type="primary" size="large" style="width: 160px" @click="$router.push('/extract')"></Button>
          </Col>
        </Row>
      </div>
    </div>
    <div class="qa-list">
      <div class="inner-box">
        <h2><Icon type="android-compass" color="#2d8cf0" /></h2>
        <Row :gutter="16">
          <Col span="12">
          <Card>
            <h3 slot="title"></h3>
            <a href="#" slot="extra"> &raquo;</a>
            <ul>
              <li v-for="item in questionList">
                <a :href="item.url" target="_blank">&rsaquo; {{item.name}}</a>
              </li>
            </ul>
          </Card>
          </Col>
          <Col span="12">
          <Card>
            <h3 slot="title"></h3>
            <a href="#" slot="extra"> &raquo;</a>
            <ul>
              <li v-for="item in questionList">
                <a :href="item.url" target="_blank">&rsaquo; {{item.name}}</a>
              </li>
            </ul>
          </Card>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageKit',
  data () {
    return {
      questionList: []
    }
  },
  created () {
    this.questionList = [
      {name: '', url: '#'},
      {name: '', url: '#'},
      {name: ' APP  UDID', url: '#'},
      {name: '', url: '#'}
    ]
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  position: relative;
  > img {
    display: block;
    width: 100%;
    margin: auto;
  }
  .inner-box {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 66%;
    margin-left: -33%;
    text-align: center;
  }
  h3 {
    margin-bottom: 3%;
    font-size: 36px;
    font-weight: normal;
    color: #333;
  }
  p {
    height: 48px;
    margin-bottom: 3%;
    font-size: 16px;
  }
}
.qa-list {
  padding: 45px 0;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
  }
  h2 {
    margin-bottom: 20px;
    font-weight: normal;
    font-size: 30px;
    color: #2d8cf0;
    .ivu-icon {
      margin-right: 5px;
    }
  }
  a {
    color: #999;
  }
  li {
    font-size: 16px;
    line-height: 30px;
  }
}
</style>



