<template>
  <div class="sub-content bind-qiniu">
    <h2></h2>
    <div class="inner-box">
      <Form
        ref="formBindQiniu"
        :model="formBindQiniu"
        :rules="ruleBindQiniu"
        :label-width="120">
        <FormItem label="Access Key" prop="accesskey">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.accesskey" placeholder="-"/>
        </FormItem>
        <FormItem label="Secret Key" prop="secretkey">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.secretkey" placeholder="-" />
        </FormItem>
        <FormItem label="" prop="bucket">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.bucket" placeholder="-" />
        </FormItem>
        <FormItem label="" prop="domain">
          <Input :readonly="userInfo.user_type === '1'" v-model="formBindQiniu.domain" placeholder="---" />
        </FormItem>
        <FormItem>
          <Button
            v-if="userInfo.user_type"
            :disabled="userInfo.user_type === '1'"
            type="primary"
            @click="handleSubmit('formBindQiniu')"> </Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageBindQiniu',
  props: {
    userInfo: Object
  },
  data () {
    return {
      formBindQiniu: {
        accesskey: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.accesskey,
        secretkey: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.secretkey,
        bucket: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.bucket,
        domain: this.userInfo.qiniu_setting && this.userInfo.qiniu_setting.domain
      },
      ruleBindQiniu: {
        accesskey: [
          {required: true, message: 'Access Key ', trigger: 'click'}
        ],
        secretkey: [
          {required: true, message: 'Secret Key ', trigger: 'click'}
        ],
        bucket: [
          {required: true, message: '', trigger: 'click'}
        ],
        domain: [
          {required: true, message: '', trigger: 'click'}
        ]
      }
    }
  },
  created () {
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$ajax.post('/api/userInfo/bindQiNiu', this.formBindQiniu).then((res) => {
            if (res.data && res.data.code === '200' && res.data.data.ret === '1') {
              this.$Message.success({
                content: '',
                duration: 1,
                onClose () {
                  window.location.reload()
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



