<template>
  <div class="sub-content coupon">
    <h2></h2>
    <div class="inner-box">
      <div class="coupon-box">
        <Alert> {{count}}  <span style="color:#ed3f14">{{value}}</span> </Alert>
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
      <div class="coupon-qa">
        <h3></h3>
        <ul>
          <li>
            <h4>1. </h4>
            <p></p>
          </li>
          <li>
            <h4>2. </h4>
            <p></p>
          </li>
          <li>
            <h4>3. </h4>
            <p></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageCoupon',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0,
      count: 1,
      value: '5.00',
      columns: [
        {title: '', key: '1'},
        {title: '', key: '2'},
        {title: '', key: '3'},
        {title: '', key: '4'},
        {title: '', key: '5'},
        {title: '', key: '6'}
      ],
      list: []
    }
  },
  created () {
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.coupon-box {
  margin: 20px 0;
}
.coupon-qa {
  margin-top: 20px;
  h3 {
    height: 40px;
    margin-bottom: 10px;
    border-bottom: 1px solid #ddd;
    line-height: 40px;
  }
  ul {
    width: 60%;
    li {
      padding: 10px 0;
      border-bottom: 1px dashed #ddd;
      h4 {
        height: 30px;
        line-height:30px;
      }
      p {
        color: #999;
      }
    }
    li:last-child {
      border-bottom: 0;
    }
  }
}
</style>



