<template>
  <div class="sub-content invoice">
    <h2></h2>
    <div class="inner-box">
      <p style="margin-bottom:10px;text-align:right"><Button type="primary" @click="handleApply"></Button></p>
      <Alert></Alert>
      <div class="invoice-box">
        <Table stripe :columns="columns" :data="list"></Table>
      </div>
      <Page
        v-if="total > 10"
        :total="total"
        show-total
        show-elevator
        @on-change="handlePageChange"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageInvoice',
  props: {
    userInfo: Object
  },
  data () {
    return {
      page: 1,
      total: 0,
      columns: [
        {title: '', key: '1'},
        {title: '', key: '2'},
        {title: '', key: '3'},
        {title: '', key: '4'},
        {title: '', key: '5'},
        {title: '', key: '6'},
        {title: '', key: '7'},
        {title: '', key: '8'},
        {title: '', key: '9'},
        {
          title: '',
          key: '10',
          render: (h, params) => {
            return h('Button', {
              props: {
                type: 'primary'
              },
              on: {
                click: () => {
                  this.handleWatchDetail(params.index)
                }
              }
            }, '')
          }
        }
      ],
      list: []
    }
  },
  created () {
  },
  methods: {
    handlePageChange (page) {
      this.page = page
    },
    handleApply () {
    },
    handleWatchDetail (index) {
      // TODO
      console.log(index)
      this.$router.push(`/invoice_detail/${index}`)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-box {
  span {
    margin: 0 5px;
  }
  button {
    margin-left: 10px;
  }
}
.invoice-box {
  margin: 20px 0;
}
</style>



