<template>
  <div class="sign">
    <div class="banner">
      <img src="../assets/images/banner_sign.jpg" alt="">
      <Button type="primary" size="large" style="width: 160px" @click="handleOpenWpa"></Button>
    </div>
    <div class="service-intro-1">
      <div class="inner-box">
        <Row>
          <Col span="8">
          <img src="../assets/images/icon_home_1.png" alt="">
          <h4>TestFlight</h4>
          <p>100%</p>
          <p></p>
          <p></p>
          </Col>
          <Col span="8">
          <img src="../assets/images/icon_home_0.png" alt="">
          <h4></h4>
          <p></p>
          <p></p>
          <p>///</p>
          </Col>
          <Col span="8">
          <img src="../assets/images/icon_home_2.png" alt="">
          <h4></h4>
          <p>UDID</p>
          <p></p>
          <p></p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-2">
      <h3></h3>
      <div class="inner-box">
        <Table border :columns="tableColumns" :data="tableData"/>
      </div>
    </div>
    <div class="service-intro-3">
      <img src="../assets/images/bg_sign_0.jpg" alt="">
    </div>
    <div class="service-intro-4">
      <div class="inner-box">
        <Row>
          <Col span="6">
          <img src="../assets/images/icon_sign_0.png" alt="">
          <h4></h4>
          <p>6iOS</p>
          <p></p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_1.png" alt="">
          <h4>&</h4>
          <p></p>
          <p></p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_2.png" alt="">
          <h4>&</h4>
          <p></p>
          <p></p>
          </Col>
          <Col span="6">
          <img src="../assets/images/icon_sign_3.png" alt="">
          <h4>7*24</h4>
          <p>24</p>
          <p></p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-5" :style="`background:#fff url(${imgs[0]}) no-repeat 25% 50%/31.58% auto`">
      <div class="inner-box">
        <Row>
          <Col span="12" offset="12">
          <h3></h3>
          <p></p>
          <p>&UDID</p>
          <p>,</p>
          </Col>
        </Row>
      </div>
    </div>
    <div class="service-intro-6">
      <img src="../assets/images/bg_sign_2.jpg" alt="">
      <Button type="primary" size="large" style="width: 160px" @click="handleOpenWpa"></Button>
    </div>
  </div>
</template>

<script>
import bg0 from '../assets/images/bg_sign_1.png'

export default {
  name: 'PageSign',
  data () {
    return {
      tableColumns: [
        {
          title: ' ',
          key: 'key0'
        },
        {
          title: 'TestFlight',
          key: 'key1'
        },
        {
          title: '',
          key: 'key2'
        },
        {
          title: 'UDID',
          key: 'key3'
        }
      ],
      tableData: [
        {
          key0: '',
          key1: '',
          key2: '',
          key3: ''
        },
        {
          key0: '',
          key1: '',
          key2: '',
          key3: 'UDID'
        },
        {
          key0: '',
          key1: 'APP',
          key2: 'APP',
          key3: 'APP'
        },
        {
          key0: '',
          key1: '100 /100',
          key2: '500/1',
          key3: '50/1'
        }
      ],
      imgs: [bg0]
    }
  },
  methods: {
    handleOpenWpa () {
      window.open('http://wpa.qq.com/msgrd?v=3&uin=3261682395&site=qq&menu=yes')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.banner {
  position: relative;
  img {
    display: block;
    width: 100%;
    margin: auto;
  }
  button {
    position: absolute;
    top: 65%;
    left: 23%;
    // margin: -18px 0 0 -79px;
  }
}
.service-intro-1 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
    overflow: hidden;
    .ivu-col {
      margin-left: -1px;
      border-left: 1px solid #eee;
    }
    img {
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
      font-weight: normal;
      font-size: 24px;
      color: #333;
    }
    p {
      font-size: 16px;
    }
  }
}
.service-intro-2 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  h3 {
    margin-bottom: 40px;
    font-size: 36px;
    font-weight: normal;
    color: #333;
  }
  .inner-box {
    width: 70%;
    margin: auto;
    .ivu-table-cell span {
      font-size: 16px;
    }
  }
}
.service-intro-3 {
  background: #2e88fe;
  img {
    display: block;
    width: 100%;
    max-width: 2000px;
    margin: auto;
  }
}
.service-intro-4 {
  padding: 45px 0;
  text-align: center;
  background: #fff;
  .inner-box {
    width: 70%;
    margin: auto;
    img {
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
      font-weight: normal;
      font-size: 18px;
      color: #333;
    }
  }
}
.service-intro-5 {
  .inner-box {
    width: 70%;
    margin: auto;
    padding: 185px 0 160px;
    border-top: 1px solid #eee;
  }
  h3 {
    margin:0 0 20px 160px;
    font-size: 36px;
    font-weight: normal;
  }
  p {
    margin: 0 0 10px 160px;
    font-size: 18px;
  }
}
.service-intro-6 {
  position: relative;
  img {
    display: block;
    width: 100%;
    max-width: 2000px;
    margin: auto;
  }
  button {
    position: absolute;
    top: 70%;
    left: 50%;
    margin-left:-79px;
  }
}
</style>



