<template>
  <div class="content app-list-create">
    <div class="wrapper">
      <Breadcrumb class="bread-crumb">
        <BreadcrumbItem to="/release"></BreadcrumbItem>
        <BreadcrumbItem to="/apps_list"></BreadcrumbItem>
        <BreadcrumbItem>{{activeText}}</BreadcrumbItem>
      </Breadcrumb>
      <Card :padding="45" shadow>
        <div class="inner-box">
          <Form
            ref="formCreate"
            :model="formCreate"
            :rules="ruleCreate"
            :label-width="120">
            <FormItem label="">
              <Upload
                ref="upload"
                :action="`//dafu.im/api/list/background`"
                :with-credentials="true"
                accept=".jpg,.jpeg,.png"
                :format="['jpg', 'jpeg', 'png']"
                :before-upload="handleBeforeUpload"
                :on-success="handleUploadSuccess"
                :on-error="handleUploadError">
                <div class="background">
                  <template v-if="background">
                    <img ref="img" :src="background" alt="">
                  </template>
                  <div :class="['upload-btn', background ? 'edit' : '']">
                    <Icon type="camera" size="40"></Icon>
                    <p>{{background ? '' : ''}}</p>
                  </div>
                </div>
              </Upload>
            </FormItem>
            <FormItem prop="title" label="">
              <Input
                type="text"
                v-model="formCreate.title"
                placeholder=""
                style="width:400px;margin-right:10px" />
              <span>APP</span>
            </FormItem>
            <FormItem prop="desc" label="">
              <Input
                type="text"
                v-model="formCreate.desc"
                placeholder=""
                style="width:400px;margin-right:10px" />
            </FormItem>
            <FormItem label="">
              <Input
                type="text"
                v-model="formCreate.remark"
                placeholder=""
                style="width:400px;margin-right:10px" />
              <span>APP</span>
            </FormItem>
            <FormItem prop="url" label="">
              <Input
                type="text"
                v-model="formCreate.url"
                placeholder="6"
                style="width:400px;margin-right:10px">
              <span slot="prepend">https://dafu.im/</span></Input>
              <span style="position:absolute;left:410px;top:0">6</span>
            </FormItem>
            <FormItem label="APP">
              <Input
                type="textarea"
                v-model="formCreate.apps"
                placeholder="http://dafu.im/a2UvMn"
                style="width:400px;margin-right:10px" />
              <span>URL10</span>
            </FormItem>
            <FormItem>
              <Button type="primary" shape="circle" style="width:120px;margin-right:10px" @click="handleSubmit('formCreate')"> </Button>
            </FormItem>
          </Form>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PageAppsListCreate',
  data () {
    return {
      background: '',
      activeText: '',
      uploadList: [],
      formCreate: {
        title: '',
        desc: '',
        url: '',
        apps: '',
        remark: ''
      },
      ruleCreate: {
        title: [
          {required: true, message: '', trigger: 'click'}
        ],
        desc: [
          {required: true, message: '', trigger: 'click'}
        ],
        url: [
          {required: true, message: '', trigger: 'click'},
          {type: 'string', min: 6, message: '6', trigger: 'click'}
        ],
        apps: [
          {required: true, message: 'App URL', trigger: 'click'}
        ]
      }
    }
  },
  created () {
    if (window.localStorage.getItem('isLogin')) {
      //  id
      const id = this.$route.query.id
      if (id) {
        this.activeText = ''
        this.formCreate.id = id
        //  id
        this.$ajax.get('/api/list/detail', {params: {id}}).then((res) => {
          if (res.data && res.data.code === '200') {
            const data = res.data.data
            this.background = data.background
            Object.keys(this.formCreate).forEach((item) => {
              this.formCreate[item] = data[item]
            })
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      } else {
        //  url
        this.$ajax.get('/api/list/getUrl').then((res) => {
          if (res.data && res.data.code === '200') {
            this.formCreate.url = res.data.data
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    } else {
      let path = this.$route.fullPath
      path = path.substring(1)
      this.$router.push(`/login#${path}`)
    }
  },
  mounted () {
    this.uploadList = this.$refs.upload.fileList
  },
  methods: {
    //  
    handleBeforeUpload () {
      const check = this.uploadList.length < 1
      if (!check) {
        this.$Message.warning(' 1 ')
      }
      return check
    },
    handleUploadSuccess (res) {
      this.background = res.data.url
      this.$refs.upload.fileList = []
      this.uploadList = []
    },
    handleUploadError (err) {
      this.$Message.error(err)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const successMsg = this.formCreate.id ? '' : ''
          this.$ajax.post('/api/list/edit', this.formCreate).then((res) => {
            if (res.data && res.data.code === '200') {
              const router = this.$router
              this.$Message.success({
                content: successMsg,
                duration: 1,
                onClose () {
                  router.push('/apps_list')
                }
              })
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.app-list-create {
  .background {
    position: relative;
    height: 250px;
    width: 576px;
    border: 1px dashed #e1f0fe;
    img {
      display: block;
      height: 100%;
      width: 100%;
    }
    .upload-btn {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -48px 0 0 -39px;
      font-size: 16px;
      text-align: center;
    }
    .edit {
      color: #fff;
    }
  }
}
</style>



